.MainPage {
    position: absolute;
    width: 1200px;
    height: 700px;
    left: 50%;
    top: 50%;
    margin-left: -600px;
    margin-top: -350px;   
    background-color: none;

    border-radius: 10px;
}
.MP_menu{
   
    position: absolute;
    left: 50%;
    margin-left: -300px;
    top: -55px;
    width: 600px;


    display: flex;
    flex-direction: row;
    justify-content: space-around;
    z-index: 0;
}
.MP_nav_but {
    width: 200px;
    height: 50px;
    font : 20px 'Times New Roman';
    z-index: -1;
    background-color: #FFF;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 3px 0 0 lightgray;
  
}
.MP_nav_but:hover {
    background-color: lightskyblue;
    box-shadow: 2px 5px 0 0 lightgray;
}