.TestTemplateA{
    position: absolute;
    left: 0;
    top: 0;
    height: 700px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    background-color: #FFF;
    border: 0.3px solid lightsteelblue;
    border-radius: 10px;
}
.TTA_quest {
    width: 1200px; 
    text-align: center;
    word-wrap: normal;
    font: 40px "Times New Roman";    
}
.TTA_list {
    list-style-type: none;
    width: 1000px;   
    height: 400px;   
    margin-left: 50px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
}
.TTA_list li{
    font: 30px "Times New Roman";
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
 }
.TTA_ans {
    width: 30px;
    height: 30px;  
}
.TTA_ans_lab{
    padding-top: -20px;
    border: 1px solid black;
}
