.Cabinet {
        position: absolute;
        width: 1200px;
        height: 700px;
        left: 50%;
        top: 50%;
        margin-left: -600px;
        margin-top: -350px;   
        background-color: none;
    
        border-radius: 10px;    
        display: flex; 
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
}
.Cab_task{
        width: 90%;
        height: 45%;        
        overflow-y: scroll;
}
.Cab_task>thead, .Cab_res>thead {
        background-color: lightblue;
        text-align: center;
}
.Cab_res {
        width: 90%;
        height: 45%;          
}
.Cab_res>tbody, .Cab_task>tbody{   
        text-align: center;
        overflow-y: scroll;
        min-height: 0px;

        justify-content: flex-start;
}
.Cab_res>tbody>tr, .Cab_task>tbody>tr{
        max-height: 50px;
        height: 50px;
}
.Cab_task_but{
        width: 100px;
        height: 45px;
        border-radius: 10px;
        background-color: lightsteelblue;
        border: none;
        font: 20 "Times New Roman"
}