.LoginPage {
    position: absolute;
    width: 400px;
    height: 550px;
    top: 50%;
    left: 50%;
    margin-left: -200px;
    margin-top: -275px;
   
    background-color: #FFF;
    border: 0.3px solid lightsteelblue;
    border-radius: 10px;
    
    display: flex;

    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.LP_login, .LP_pass {
    text-align: left;
    height: 50px;    
    border: none;
    border-bottom: 1px solid black;    
    margin-top: 5px;
    font: 30px "Lucida Console";
    width: 80%;
    background-color: #FFF;
}
.LP_but{
    height: 50px;
    border: none;
    width: 80%;
    border-radius: 10px;
    margin-top: 10px;    
    background-color: lightsteelblue;
    box-shadow: 1px 1px 0px 0px lightgray;
    font: 30px "Lucida Console";
    color: #FFF;
    
}
.LP_but:hover{
    box-shadow: 2px 3px 3px 3px lightgray;
    background-color: lightskyblue;
}
.LP_header {
    width: 80%;
    text-align: center;
    font: 50px "Franklin Gothic";
    background-color: #FFF;
}
.LP_logo{

    width: 50px;
    height: 50px;
    left: 125px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: #FFF;
}